<template>
  <div>
    <div class="title" style="font-size: 1rem">{{title}}</div>
    <div>{{ description }}</div>
    <InputNumber v-model="integer"
           :max="max"
           :min="min"
           :step="1"
           @on-blur="checkValidate"
           @on-change="inputChange()"
           :readonly="readonly">
    </InputNumber>
  </div>
</template>

<script>
import validate from '@/api/validate'
// id="integer-text"
// :class="isCorrect ? 'correct': 'incorrect'"
export default {
  name: 'integer-input',
  data () {
    return {
      uuid: '00000000-0000-0000-0000-0000000000015',
      integer: '',
      isCorrect: true
    }
  },
  props: {
    schemaKey: {
      type: String,
      require: true
    },
    title: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    description: {
      type: String,
      default: ''
    },
    min: {
      type: Number,
      default: -1.7976931348623157E+308
    },
    max: {
      type: Number,
      default: 1.7976931348623157E+308
    },
    maxLength: {
      type: Number,
      default: 200
    },
    validate: {
      type: Boolean
    },
    readonly: {
      type: Boolean
    },
    readonlyData: {
      type: Array,
      default: () => []
    },
    schemaType: {
      type: String
    },
    apiType: {
      type: String
    },
    server: {
      type: Boolean,
      require: false
    }
  },
  mounted () {
    if (this.value || (!this.value && this.value == 0)) { // eslint-disable-line
      this.integer = parseInt(this.value)
      this.inputChange(true)
    }
  },
  methods: {
    async inputChange (first) {
      this.$emit('input', {
        schemaKey: this.schemaKey,
        value: this.integer,
        server: this.server,
        first
      })
    },
    async checkValidate () {
      if (this.validate) {
        const tmp = {}
        tmp[this.schemaKey] = this.integer
        const data = await validate(this.schemaType, this.apiType, {
          projectId: this.$route.query.projectId,
          valueJson: JSON.stringify(tmp)
        })

        if (data.response.success === false && data.response.msg) {
          this.$Message.error(data.response.msg)
        } else if (data.response.success === true && data.response.msg) {
          this.$Message.info(data.response.msg)
        }
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.correct
  color #CA243C

.incorrect
  color #CC2540

  .pop-message
    color #CC2540
</style>
